<template>
	<VideoPlayer :v-id="vid" :embed-url="vembed"/>
	<VideosList class="hidden"/>
</template>
<script>
import VideoPlayer from '@/components/VideoPlayer.vue';
import VideosList from '@/components/VideosList.vue';

import { useRoute } from 'vue-router';
export default {
	data() {
		return {
			vid: useRoute().params.id,
			vembed: 'https://www.eporner.com/embed/' + useRoute().params.id
		}
	},
    components: { VideoPlayer, VideosList },
}
</script>