export default class VideoManagement {

    //#region variaveis
    URL_BASE = "https://www.eporner.com/api/v2/video/"
    TITLE_SIZE = 57 //default: 57 chars
    API_DEFAULT_CONFIG = {
        id: "",
        query: "",
        per_page: "12",
        page: "2",
        thumbsize: "small",
        order: "latest", //"top-weekly",
        gay: "0",
        lq: "0",
        format: "json",
    }
    //#endregion

    //#region Setter's
    setID(_id) { this.API_DEFAULT_CONFIG.id = _id;}
    setQuery(_query) { this.API_DEFAULT_CONFIG.query = _query;}
    setVideosPerPage(_per_page) { this.API_DEFAULT_CONFIG.per_page = _per_page;}
    setCurrentPage(_page) { this.API_DEFAULT_CONFIG.page = _page;}
    setThumbSize(_thumbsize) { this.API_DEFAULT_CONFIG.thumbsize = _thumbsize;}
    setOrder(_order) { this.API_DEFAULT_CONFIG.order = _order;}
    setGay(_gay) { this.API_DEFAULT_CONFIG.gay = _gay;}
    setLowQuality(_lq) { this.API_DEFAULT_CONFIG.lq = _lq;}
    setOutputFormat(_format) { this.API_DEFAULT_CONFIG.format = _format;}
    // setVideoDiv(_div_name) { VIDEOS_DIV = _div_name;}
    //#endregion

    async showConfigs()
    {
        console.log('[Configuração]\n' + JSON.stringify(this.API_DEFAULT_CONFIG, null, 4))
    }

    async fetchVideos()
    {
        const res = await this.getVideos()
        return res
    }

    /**
     * Se informado o ID != 0, retorna o vídeo especificado, 
     * senão retorna vários vídeos
     */
    async getVideos(id = 0)
    {
        let url = this.URL_BASE

        if (id == 0) {
            url += "search/?";
            if (this.API_DEFAULT_CONFIG.query != "")
                url += "query=" + this.API_DEFAULT_CONFIG.query

            url += "&per_page=" + this.API_DEFAULT_CONFIG.per_page
            url += "&page=" + this.API_DEFAULT_CONFIG.page
            url += "&order=" + this.API_DEFAULT_CONFIG.order
            url += "&gay=" + this.API_DEFAULT_CONFIG.gay
            url += "&lq=" + this.API_DEFAULT_CONFIG.lq
            url += "&format=" + this.API_DEFAULT_CONFIG.format
        } else {
            url += +"/id/?id=" + id
        }

        //define o tamanho das imgs dos vídeos
        url += "&thumbsize=" + this.API_DEFAULT_CONFIG.thumbsize

        return await fetch(url).then(res => res.json())
    }
}