<template>
    <div id="popularCategories" class="w-5/6 mx-auto mt-14 flex flex-col">
			<div id="cards" class="grid grid-cols-1 mx-auto gap-5 tablet:grid-cols-2 laptop:grid-cols-3 desktop:grid-cols-4 2xl:grid-cols-5">
                <div class="mb-6 col-span-1 tablet:col-span-2 laptop:col-span-3 desktop:col-span-4 2xl:col-span-5 tablet:flex justify-between">
                    <span class="font-semibold text-lg tablet:text-2xl text-white">
                        {{ title }}
                    </span>
                    <a href="#" class="cursor-pointer">
                        <span class="text-[#F1931D] text-sm font-semibold flex gap-x-2">
                            More Videos
                            <img class="-rotate-90 mt-1 fill-[#F1931D]" src="@/assets/imgs/icons/chevron3.svg" alt="">
                        </span>
                    </a>
                </div>
                <VideoCard 
                    v-for="video in lvideos" 
                    :key="video.id"
                    :video-data="video"
                />
			</div>
		</div>
</template>
<script>
import VideoCard from "@/components/VideoCard.vue";
// import axios from 'axios'
import VideoManagement from '@/classes/videoManagement.class'

export default {
    props: {
        vorder: {
            type: Number,
            default: 0,
            required: true
        }
    },
    data() {
        return {
            title: '',
            videoPerPage: 20,
            page: 1,
            lvideos: [
                {
                    id: '',
                    title: "Lorem ipsum dolor sit amet, consectetur adipiscing",
                    embed: '',
                    keywords: [ 'a','b' ],
                    length_min: '',
                    views: 1,
                    rate: '76%',
                    uploader: "Pornster XXX",
                },
            ],
        };
    },
    components: { VideoCard },
    async mounted() {
        const order = [
            ['latest', 'Latest Videos'],
            ['longest', 'Longest Videos'],
            ['shortest', 'Shortest Videos'],
            ['top-rated', 'Top Rated Videos'],
            ['most-popular', 'Most Popular Videos'],
            ['top-weekly', 'Top Weekly Videos'],
            ['top-monthly', 'Top Monthly Videos']
        ]
        
        this.title = order[this.vorder][1]

        let videos = new VideoManagement()
        videos.setVideosPerPage(this.videoPerPage)
        videos.setCurrentPage(this.page)
        videos.setOrder(order[this.vorder][0])
        
        await videos.fetchVideos().then((res) => {
            this.lvideos = res.videos
        })
    },
}
</script>