<template>
    <div class="w-[270px] flex flex-col gap-y-2">
        <div class="w-[270px] h-[150px] rounded-[5px] flex items-end justify-end">
            <router-link :to="v_link" class="w-full h-full" @mouseenter="showVideoPreview" @mouseleave="hideVideoPreview">

                <video :id="'preview-' + v_id" :ref="'preview-' + v_id" v-show="v_preview" poster="@/assets/imgs/video_loading_bg_icon.gif" class="rounded-lg w-full h-full object-center object-cover lg:w-full lg:h-full" onMouseLeave="OnVideoMouseLeave(this.id)" onMouseEnter="OnVideoMouseEnter(this.id)"> <!-- preload="none" poster="${video_thumb_src}" -->
                    <source :src="v_animThumbSrc"/>
                    Your browser does not support the video tag.
                </video>
                <img :id="'thumb-' + v_id" v-show="!v_preview" class="w-full h-full object-cover object-center rounded-lg mx-auto z-10 float-left" 
                :src="v_thumbSrc" alt="">

            </router-link>
            <span class="text-white font-bold text-sm bg-black/50 px-2 py-1 z-20 absolute">
                {{ v_length_min }}
            </span>
        </div>
        <div class="w-full text-[#D5D6D7] text-lg">
            {{ v_title }}
        </div>
        <div class="w-full flex justify-between pr-2 items-center h-5">
            <div class="text-[#F962D8] font-semibold">
                {{ v_uploader }}
            </div>
            <div class="text-[#8D8B8F] flex gap-x-2 items-center">
                <img width="16" src="@/assets/imgs/icons/view_icon.svg" alt="">
                {{ v_views }}
            </div>
            <div class="text-[#8D8B8F] flex gap-x-2 items-center">
                <img width="16" src="@/assets/imgs/icons/like_icon.svg" alt="">
                {{ v_rate }}
            </div>
        </div>
    </div>
</template>

<script>

export default{
    props: {
        videoData: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            v_id: this.videoData.id,
            v_title: this.videoData.title,
            v_thumbSrc: this.getThumb(),
            v_animThumbSrc: this.getPreviewThumb(),
            v_embed: this.videoData.embed,
            v_keywords: this.videoData.keywords,
            v_length_min: this.videoData.length_min,
            v_views: this.calculateViews(),
            v_rate: this.calculateRate(),
            v_uploader: this.uploaderCheck(),
            v_preview: false,
            v_element: null,
            v_link: this.setVideoLink()
        }
    },
    methods: {
        calculateRate: function() {
            if(parseFloat(this.videoData.rate) == 0.0)
                return Math.floor(Math.random() * 10) + 72+'%'
            return parseFloat((this.videoData.rate/5).toFixed(2))*100 + "%"
        },
        calculateViews: function() {
            let views = this.videoData.views
            if(views > 999)
                return parseFloat((this.videoData.views/1000).toFixed(2)) + " K"
            else if(views > 999999)
                return parseFloat((this.videoData.views/1000000).toFixed(2)) + " M"
            return this.videoData.views
        },
        uploaderCheck: function() {
            if(this.videoData.uploader == null)
                return "SNR Pornister"
            return this.videoData.uploader
        },
        getThumb: function() {
            if(typeof this.videoData.default_thumb !== 'undefined')                
                return this.videoData.default_thumb.src
            return ''
        },
        getPreviewThumb: function() {
            if(typeof this.videoData.default_thumb == 'undefined')                
                return
            
            const src = this.videoData.default_thumb.src
            let url = src.replace("//", "*"); //define a url
            let url_splited = url.split("/"); //divide a url
            url_splited.pop(); //remove o último index

            //definindo o video de prévia
            let video_dir = url_splited[url_splited.length - 1];
            let videoPreviewName = video_dir + "-preview.mp4";

            //montando a nova url
            let new_url = url_splited.join("/").replace("*", "//");
            new_url += "/" + videoPreviewName;

            return new_url;
        },
        showVideoPreview: function() {
            this.v_animThumbSrc = this.getPreviewThumb(this.v_thumbSrc)
            this.v_preview = true
            var isPlaying = this.v_element.currentTime > 0 && !this.v_element.paused && !this.v_element.ended  && this.v_element.readyState > this.v_element.HAVE_CURRENT_DATA;
            if (!isPlaying) {
                this.v_element.muted = true;
                this.v_element.currentTime = 1;
                this.v_element.playbackRate = 0.85;
                this.v_element.play();
            }
        },
        hideVideoPreview: function() {
            this.v_preview = false
            var isPlaying = this.v_element.currentTime > 0 && !this.v_element.paused && !this.v_element.ended  && this.v_element.readyState > this.v_element.HAVE_CURRENT_DATA;
            if (isPlaying) {
                this.v_element.currentTime = 0;
                this.v_element.playbackRate = 1;
                this.v_element.pause();
            }
        },
        setVideoLink: function() {
            return `/play/${this.videoData.id}`
            //return "/play/" + this.videoData.id
            //this.v_link = { name: 'play', params: { id: 2 } }
        }
    },
    setup() {
    },
    mounted() {
        //const video = document.getElementById('preview'+this.v_id)
        this.v_element = this.$refs["preview-"+this.v_id]
    },

}
</script>